<template>
  <div class="row mt-0 d-flex align-items-start" id="login-page">
    <div class="col-12 flex-center">
      <div class="flex-center login-box box flex-column">
        <h5>Activating account</h5>
        <form @submit.prevent="onSubmit" class="w-100">
          <label for="Email" class="custom-label">Email</label>
          <el-input
            name="Email"
            placeholder="Enter your email"
            class="w-100 mb-3"
            v-model="email"
          />
          <el-button
            :loading="isLoading"
            class="w-100 mx-0 mb-3"
            type="primary"
            style="width: 100% !important"
            native-type="primary"
            >Send varification link</el-button
          >
        </form>
        <div class="flex-center w-100 my-2">
          <!-- <a class="primary pointer md-text">Create new account</a> -->
          <a
            class="primary hover-underline pointer md-text"
            @click="$router.go(-1)"
            >Back</a
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: "",
      password: "",
      isLoading: false,
    };
  },
  created() {
    this.email = this.$route.query.email;
  },
  methods: {
    onSubmit() {
      this.isLoading = true;
      this.$axios
        .get("activate-user/?email=" + this.email)
        .then((res) => {
          this.isLoading = false;
          this.$vs.notification({
            color: "success",
            title: "Success",
            text: res.data,
          });
        })
        .catch((err) => {
          this.isLoading = false;
          this.$error.raise(err);
        });
    },
  },
};
</script>